import React, { useState } from 'react';
import { useEffect } from 'react';
import axios from 'axios';
import { useSelector, useDispatch } from 'react-redux';
import Header from './Header';
import { Doughnut } from 'react-chartjs-2';
import ChartDataLabels from 'chartjs-plugin-datalabels';

function Pacientes() {
  var html = 'https://passomatrix-server.herokuapp.com';
  // var html = '//localhost:3000';
  var quotes = "'";

  const changeState = useDispatch();

  const loginLogin = useSelector((state) => state.userlogin);
  const loginUsuario = useSelector((state) => state.username);
  const loginCti = useSelector((state) => state.cti);
  const viewPacientes = useSelector((state) => state.pacientes);

  // dados do paciente.
  var box = useSelector((state) => state.box);
  var entrada = useSelector((state) => state.entrada);
  var nome = useSelector((state) => state.nome);
  var dn = useSelector((state) => state.dn);
  var hd = useSelector((state) => state.hd);
  var ap = useSelector((state) => state.ap);
  var alergias = useSelector((state) => state.alergias);
  var medprev = useSelector((state) => state.medprev);
  var exprev = useSelector((state) => state.exprev);
  var hda = useSelector((state) => state.hda);
  var status = useSelector((state) => state.status);
  var inter = useSelector((state) => state.inter);

  // invasões.
  var va = useSelector((state) => state.va);
  var dataVa = useSelector((state) => state.datava);
  var jid = useSelector((state) => state.jid);
  var dataJid = useSelector((state) => state.datajid);
  var jie = useSelector((state) => state.jie);
  var dataJie = useSelector((state) => state.datajie);
  var subcld = useSelector((state) => state.subcld);
  var dataSubcld = useSelector((state) => state.datasubcld);
  var subcle = useSelector((state) => state.subcle);
  var dataSubcle = useSelector((state) => state.datasubcle);
  var radd = useSelector((state) => state.radd);
  var dataRadd = useSelector((state) => state.dataradd);
  var rade = useSelector((state) => state.rade);
  var dataRade = useSelector((state) => state.datarade);
  var vfemd = useSelector((state) => state.vfemd);
  var dataVfemd = useSelector((state) => state.datavfemd);
  var vfeme = useSelector((state) => state.vfeme);
  var dataVfeme = useSelector((state) => state.datavfeme);
  var afemd = useSelector((state) => state.afemd);
  var dataAfemd = useSelector((state) => state.dataafemd);
  var afeme = useSelector((state) => state.afeme);
  var dataAfeme = useSelector((state) => state.dataafeme);
  var snc = useSelector((state) => state.snc);
  var dataSnc = useSelector((state) => state.datasnc);
  var torax = useSelector((state) => state.torax);
  var dataTorax = useSelector((state) => state.datatorax);
  var abd = useSelector((state) => state.abd);
  var dataAbd = useSelector((state) => state.dataabd);
  var svd = useSelector((state) => state.svd);
  var dataSvd = useSelector((state) => state.datasvd);

  // vm.
  var vm = useSelector((state) => state.vm);
  var pp = useSelector((state) => state.pp);
  var peep = useSelector((state) => state.peep);
  var fi = useSelector((state) => state.fi);

  const [pacientes, setPacientes] = useState([]);

  // função que seleciona um paciente.
  const selectItem = (item) => {
    var box = item.box;
    var entrada = item.entrada;
    var nome = item.nome;
    var dn = item.dn;
    var hd = item.hd;
    var ap = item.ap;
    var alergias = item.alergias;
    var medprev = item.medprev;
    var exprev = item.exprev;
    var hda = item.hda;
    var status = item.status;
    var inter = item.inter;
    var va = item.va;
    var dataVa = item.datava;
    var jid = item.jid;
    var dataJid = item.datajid;
    var jie = item.jie;
    var dataJie = item.datajie;
    var subcld = item.subcld;
    var dataSubcld = item.datasubcld;
    var subcle = item.subcle;
    var dataSubcle = item.datasubcle;
    var radd = item.radd;
    var dataRadd = item.dataradd;
    var rade = item.rade;
    var dataRade = item.datarade;
    var vfemd = item.vfemd;
    var dataVfemd = item.datavfemd;
    var vfeme = item.vfeme;
    var dataVfeme = item.datavfeme;
    var afemd = item.afemd;
    var dataAfemd = item.dataafemd;
    var afeme = item.afeme;
    var dataAfeme = item.dataafeme;
    var svd = item.svd;
    var dataSvd = item.datasvd;
    var torax = item.torax;
    var dataTorax = item.datatorax;
    var abd = item.abd;
    var dataAbd = item.dataabd;
    var snc = item.snc;
    var dataSnc = item.datasnc;
    var vm = item.vm;
    var pp = item.pp;
    var peep = item.peep;
    var fi = item.fi;

    changeState({
      type: 'CORRIDA_ON',
      payloadUserLogin: loginLogin,
      payloadUserName: loginUsuario,
      payloadCti: loginCti,
      payloadIdpct: item.id,
      payloadBox: box,
      payloadEntrada: entrada,
      payloadNome: nome,
      payloadDn: dn,
      payloadHd: hd,
      payloadAp: ap,
      payloadAlergias: alergias,
      payloadMedprev: medprev,
      payloadExprev: exprev,
      payloadHda: hda,
      payloadStatus: status,
      payloadVa: va,
      payloadDatava: dataVa,
      payloadJid: jid,
      payloadDatajid: dataJid,
      payloadJie: jie,
      payloadDatajie: dataJie,
      payloadSubcld: subcld,
      payloadDatasubcld: dataSubcld,
      payloadSubcle: subcle,
      payloadDatasubcle: dataSubcle,
      payloadRadd: radd,
      payloadDataradd: dataRadd,
      payloadRade: rade,
      payloadDatarade: dataRade,
      payloadVfemd: vfemd,
      payloadDatavfemd: dataVfemd,
      payloadVfeme: vfeme,
      payloadDatavfeme: dataVfeme,
      payloadAfemd: afemd,
      payloadDataafemd: dataAfemd,
      payloadAfeme: afeme,
      payloadDataafeme: dataAfeme,
      payloadSvd: svd,
      payloadDatasvd: dataSvd,
      payloadSnc: snc,
      payloadDatasnc: dataSnc,
      payloadTorax: torax,
      payloadDatatorax: dataTorax,
      payloadAbd: abd,
      payloadDataabd: dataAbd,
      payloadVm: vm,
      payloadPp: pp,
      payloadPeep: peep,
      payloadFi: fi,
      payloadInter: inter,
    });
  };

  // função que adiciona um paciente.
  const insertPaciente = () => {
    changeState({
      type: 'INSERT_PACIENTE',
      payloadUserLogin: loginLogin,
      payloadUserName: loginUsuario,
      payloadCti: loginCti,
    });
  };

  // função que atualiza um paciente.
  const updateItem = (item) => {
    changeState({
      type: 'UPDATE_PACIENTE',
      payloadUserLogin: loginLogin,
      payloadUserName: loginUsuario,
      payloadCti: loginCti,
      payloadIdpct: item.id,
      payloadBox: item.box,
      payloadEntrada: item.entrada,
      payloadNome: item.nome,
      payloadDn: item.dn,
      payloadHd: item.hd,
      payloadAp: item.ap,
      payloadAlergias: item.alergias,
      payloadMedprev: item.medprev,
      payloadExprev: item.exprev,
      payloadHda: item.hda,
      payloadStatus: item.status,
      payloadVa: item.va,
      payloadDatava: item.datava,
      payloadJid: item.jid,
      payloadDatajid: item.datajid,
      payloadJie: item.jie,
      payloadDatajie: item.datajie,
      payloadSubcld: item.subcld,
      payloadDatasubcld: item.datasubcld,
      payloadSubcle: item.subcle,
      payloadDatasubcle: item.datasubcle,
      payloadRadd: item.radd,
      payloadDataradd: item.dataradd,
      payloadRade: item.rade,
      payloadDatarade: item.datarade,
      payloadVfemd: item.vfemd,
      payloadDatavfemd: item.datavfemd,
      payloadVfeme: item.vfeme,
      payloadDatavfeme: item.datavfeme,
      payloadAfemd: item.afemd,
      payloadDataafemd: item.dataafemd,
      payloadAfeme: item.afeme,
      payloadDataafeme: item.dataafeme,
      payloadSvd: item.svd,
      payloadDatasvd: item.datasvd,
      payloadSnc: item.snc,
      payloadDatasnc: item.datasnc,
      payloadTorax: item.torax,
      payloadDatatorax: item.datatorax,
      payloadAbd: item.abd,
      payloadDataabd: item.dataabd,
      payloadVm: vm,
      payloadPp: pp,
      payloadPeep: peep,
      payloadFi: fi,
      payloadInter: inter,
    });
  };

  // carregamento da lista e preparação do gráfico que distribui os pacientes por status.
  const [pacientesEstavel, setPacientesEstavel] = useState([]);
  const [pacientesAlerta, setPacientesAlerta] = useState([]);
  const [pacientesInstavel, setPacientesInstavel] = useState([]);

  // carregamento inicial da lista de pacientes.
  const [fade, setFade] = useState('secondary fade-in');
  useEffect(() => {
    if (viewPacientes === 1) {
      axios
        .get(html + '/pacientes/' + quotes + loginCti + quotes)
        .then((response) => {
          setPacientes(response.data);
        });
      console.log('LISTA DE PACIENTES CARREGADA');
      axios
        .get(html + '/pacientesinstavel/' + quotes + loginCti + quotes)
        .then((response) => {
          setPacientesInstavel(response.data);
        });
      axios
        .get(html + '/pacientesalerta/' + quotes + loginCti + quotes)
        .then((response) => {
          setPacientesAlerta(response.data);
        });
      axios
        .get(html + '/pacientesestavel/' + quotes + loginCti + quotes)
        .then((response) => {
          setPacientesEstavel(response.data);
        });
      setTimeout(() => {
        setFade('secondary');
      }, 500);
    }
    // eslint-disable-next-line
  }, [viewPacientes]);

  // GRÁFICO DE DISTRIBUIÇÃO DE PACIENTES POR STATUS.
  const dataChart = {
    labels: [' INSTÁVEL', ' ALERTA', ' ESTÁVEL'],
    datasets: [
      {
        data: [
          pacientesInstavel.length,
          pacientesAlerta.length,
          pacientesEstavel.length,
        ],
        backgroundColor: ['#F1948A', '#F7DC6F', '#7DCEA0'],
      },
    ],
  };

  function Chart() {
    return (
      <div className="secondary" style={{ width: 220, height: 220 }}>
        <Doughnut
          data={dataChart}
          width={200}
          height={200}
          plugins={ChartDataLabels}
          options={{
            animation: {
              duration: 0,
            },
            plugins: {
              datalabels: {
                display: false,
              },
            },
            title: {
              display: false,
              text: 'STATUS DOS PACIENTES:',
            },
            legend: {
              display: false,
              position: 'bottom',
            },
            maintainAspectRatio: true,
            responsive: false,
          }}
        ></Doughnut>
      </div>
    );
  }

  // MODAL.
  const [modalTrigger, setModalTrigger] = useState(0);
  const [modalMessage, setModalMessage] = useState('');
  const [modalType, setModalType] = useState(0);
  const [modalId, setModalId] = useState(0); // id do registro selecionado na lista.
  // função para confirmar exclusão do paciente.
  const clickDeletePaciente = (id) => {
    setFade('secondary fade-in');
    //ANIMAÇÃO DO GRÁFICO
    setModalTrigger(1);
    setModalType(1);
    setModalMessage('CONFIRMAR EXCLUSÃO DO PACIENTE?');
    setModalId(id);
    setTimeout(() => {
      setFade('secondary');
    }, 500);
  };
  // funções que ficam no modal.
  const deleteData = () => {
    if (modalType === 1) {
      // deletando primeiro os registros referentes ao paciente a ser excluído:
      fetch(html + '/deletetudo/' + modalId);
      // deletando o registro do paciente.
      fetch(html + '/deletepaciente/' + modalId);
      console.log('EXCLUINDO PACIENTE E TODOS OS SEUS REGISTROS: ' + modalId);
      setModalTrigger(0);
      toastSuccess();
    } else {
      return null;
    }
  };
  const holdData = () => {
    setModalTrigger(0);
  };
  // renderização do modal.
  function Modal() {
    if (modalTrigger === 1) {
      return (
        <div
          className={fade}
          style={{
            backgroundColor: 'rgba(0, 0, 0, 0.6)',
            borderRadius: 0,
            position: 'absolute',
            top: 0,
            bottom: 0,
            left: 0,
            right: 0,
            height: '100%',
            width: '100%',
            marginTop: 0,
            marginBottom: 0,
            marginLeft: 0,
            marginRight: 0,
            zIndex: 3,
          }}
        >
          <div
            className="secondary"
            style={{
              backgroundColor: '#FFFFFF',
              borderRadius: 5,
              paddingTop: 50,
              paddingBottom: 50,
              paddingLeft: 50,
              paddingRight: 50,
            }}
          >
            <label
              style={{
                marginTop: 5,
                marginBottom: 5,
                marginLeft: 5,
                marginRight: 5,
                color: '#2C3E50',
                fontSize: 16,
                fontWeight: 'bold',
                textAlign: 'center',
              }}
            >
              {modalMessage}
            </label>
            <div
              style={{
                display: 'flex',
                flexDirection: 'row',
                justifyContent: 'flex-start',
              }}
            >
              <button
                className="green-button"
                style={{ width: 100 }}
                onClick={() => deleteData()}
              >
                EXCLUIR
              </button>
              <button
                className="red-button"
                style={{ width: 100 }}
                onClick={() => holdData()}
              >
                CANCELAR
              </button>
            </div>
          </div>
        </div>
      );
    } else {
      return null;
    }
  }

  // TOAST.
  var toastColor = '';
  var toastMessage = '';
  const toastSuccess = () => {
    toastColor = '#52BE80';
    toastMessage = 'PACIENTE EXCLUÍDO COM SUCESSO';
    changeState({
      type: 'TOAST_SUCCESS_PACIENTES',
      payloadToastColor: toastColor,
      payloadToastMessage: toastMessage,
      payloadUserLogin: loginLogin,
      payloadUserName: loginUsuario,
      payloadCti: loginCti,
      payloadBox: box,
      payloadEntrada: entrada,
      payloadNome: nome,
      payloadDn: dn,
      payloadHd: hd,
      payloadAp: ap,
      payloadAlergias: alergias,
      payloadMedprev: medprev,
      payloadExprev: exprev,
      payloadHda: hda,
      payloadStatus: status,
      payloadVa: va,
      payloadDatava: dataVa,
      payloadJid: jid,
      payloadDatajid: dataJid,
      payloadJie: jie,
      payloadDatajie: dataJie,
      payloadSubcld: subcld,
      payloadDatasubcld: dataSubcld,
      payloadSubcle: subcle,
      payloadDatasubcle: dataSubcle,
      payloadRadd: radd,
      payloadDataradd: dataRadd,
      payloadRade: rade,
      payloadDatarade: dataRade,
      payloadVfemd: vfemd,
      payloadDatavfemd: dataVfemd,
      payloadVfeme: vfeme,
      payloadDatavfeme: dataVfeme,
      payloadAfemd: afemd,
      payloadDataafemd: dataAfemd,
      payloadAfeme: afeme,
      payloadDataafeme: dataAfeme,
      payloadSvd: svd,
      payloadDatasvd: dataSvd,
      payloadSnc: snc,
      payloadDatasnc: dataSnc,
      payloadTorax: torax,
      payloadDatatorax: dataTorax,
      payloadAbd: abd,
      payloadDataabd: dataAbd,
      payloadVm: vm,
      payloadPp: pp,
      payloadPeep: peep,
      payloadFi: fi,
      payloadInter: inter,
    });
    setTimeout(() => {
      changeState({
        type: 'PACIENTES_OFF',
        payloadUserLogin: loginLogin,
        payloadUserName: loginUsuario,
        payloadCti: loginCti,
      });
      changeState({
        type: 'PACIENTES_ON',
        payloadUserLogin: loginLogin,
        payloadUserName: loginUsuario,
        payloadCti: loginCti,
      });
    }, 3500);
  };

  // renderização da lista de pacientes.
  function ShowPacientes() {
    return (
      <div
        className="scrollpacientes"
        style={{
          justifyContent: 'flex-start',
          borderRadius: 5,
          height: 250,
          maxHeight: 250,
        }}
      >
        {pacientes.map((item) => (
          <p
            key={item.id}
            className="row"
            style={{
              position: 'relative',
              marginLeft: 0,
              marginRight: 0,
              marginTop: 0,
              marginBottom: 0,
            }}
          >
            <button
              className="grey"
              disabled="true"
              style={{ minWidth: 50, backgroundColor: "#A6ACAF" }}
              title="BOX"
            >
              {item.box}
            </button>
            <button
              className="status"
              data-status={item.status}
              title={
                'STATUS: ' +
                item.status +
                '. CLIQUE PARA EVOLUIR. \nINTERCONSULTAS: ' +
                item.inter
              }
              onClick={() => selectItem(item)}
              style={{
                marginLeft: 0,
                marginRight: 0,
                width: 450,
              }}
            >
              {item.nome}
            </button>
            <div style={{ display: 'flex', flexDirection: 'row' }}>
              <button
                className="yellow-button"
                title="EDITAR PACIENTE."
                onClick={() => updateItem(item)}
                style={{ height: 50, width: 50 }}
              >
                E
              </button>
              <button
                className="red-button"
                title="EXCLUIR PACIENTE."
                onClick={() => clickDeletePaciente(item.id)}
                style={{ marginLeft: 0, height: 50, width: 50 }}
              >
                x
              </button>
            </div>
          </p>
        ))}
      </div>
    );
  }

  if (viewPacientes === 1) {
    return (
      <div>
        <Modal></Modal>
        <Header></Header>
        <div
          id="PRINCIPAL"
          className="secondary fade-in"
          style={{
            flexDirection: 'row',
            justifyContent: 'center',
          }}
        >
          <div
            id="CHART"
            className="fade-in"
            style={{
              marginRight: 0,
              marginBottom: 0,
            }}
          >
            <p className="title2">STATUS DO CTI</p>
            <div
              style={{
                backgroundColor: '#D7DBDD',
                borderColor: '#D7DBDD',
                borderRadius: 5,
                paddingTop: 10,
                paddingBottom: 10,
                paddingLeft: 10,
                paddingRight: 10,
              }}
            >
              <Chart></Chart>
            </div>
          </div>
          <div
            id="LIST"
            className={fade}
            style={{
              justifyContent: 'flex-start',
              marginRight: 0,
            }}
          >
            <p className="title2">LISTA DE PACIENTES</p>
            <ShowPacientes></ShowPacientes>
          </div>
        </div>
        <div className="secondary">
          <button
            className="blue-button"
            onClick={() => insertPaciente()}
            title="ADICIONAR PACIENTE."
            style={{
              height: 50,
              paddingLeft: 10,
              paddingRight: 10,
            }}
          >
            ADICIONAR PACIENTE
          </button>
        </div>
      </div>
    );
  } else {
    return null;
  }
}
export default Pacientes;
