import React from 'react';
import axios from 'axios';
import { useSelector, useDispatch } from 'react-redux';
import swal from 'sweetalert';
import MaskedInput from 'react-maskedinput';
import moment from 'moment';

function InsertPaciente() {
  var html = 'https://passomatrix-server.herokuapp.com';
  // var html = '//localhost:3000';
  const changeState = useDispatch();

  const loginLogin = useSelector((state) => state.userlogin);
  const loginUsuario = useSelector((state) => state.username);
  const loginCti = useSelector((state) => state.cti);

  const viewInsertPaciente = useSelector((state) => state.insertPaciente);

  var box = '';
  var entrada = moment().format('DD/MM/YYYY');
  var nome = '';
  var dn = '';
  var hd = '';
  var ap = '';
  var alergias = '';
  var medprev = '';
  var exprev = '';
  var hda = '';
  var status = '';
  var inter = '---';

  // função que salva o novo paciente.
  const sendData = () => {
    var obj = {
      cti: loginCti,
      box: box,
      entrada: entrada,
      nome: nome,
      dn: dn,
      hd: hd,
      ap: ap,
      alergias: alergias,
      medprev: medprev,
      exprev: exprev,
      hda: hda,
      status: status,
      inter: inter,
      va: 0,
      jid: 0,
      jie: 0,
      subcld: 0,
      subcle: 0,
      radd: 0,
      rade: 0,
      vfemd: 0,
      vfeme: 0,
      afemd: 0,
      afeme: 0,
      svd: 0,
      torax: 0,
      abd: 0,
      snc: 0,
      datava: '',
      datajid: '',
      datajie: '',
      datasubcld: '',
      datasubcle: '',
      dataradd: '',
      datarade: '',
      datavfemd: '',
      datavfeme: '',
      dataafemd: '',
      dataafeme: '',
      datasvd: '',
      datatorax: '',
      dataabd: '',
      datasnc: '',
      vm: '',
      pp: '',
      peep: '',
      fi: '',
    };
    axios.post(html + '/insertpaciente', obj);
    toastSuccess();
  };

  const checkBox = () => {
    box = document.getElementById('inputBox').value;
    var quotes = '"';
    axios
      .get(
        html +
          '/pacientes/' +
          quotes +
          loginCti +
          quotes +
          '/' +
          quotes +
          box +
          quotes
      )
      .then((res) => {
        var x = [];
        x = res.data;
        const pickBox = x.map((box) => box.box);
        // eslint-disable-next-line
        if (pickBox != '') {
          console.log('ERRO: BOX JÁ OCUPADO.');
          swal({
            title: 'ERRO',
            text:
              'O box informado já está ocupado. Favor informar o box correto.',
            icon: 'warning',
            dangerMode: true,
          });
          box = '';
        } else {
          checkFields();
        }
      });
  };

  const checkFields = () => {
    box = document.getElementById('inputBox').value;
    entrada = document.getElementById('inputEntrada').value;
    nome = document.getElementById('inputNome').value.toUpperCase();
    dn = document.getElementById('inputDn').value;
    hd = document.getElementById('inputHd').value.toUpperCase();
    ap = document.getElementById('inputAp').value.toUpperCase();
    alergias = document.getElementById('inputAlergias').value.toUpperCase();
    medprev = document.getElementById('inputMedprev').value.toUpperCase();
    exprev = document.getElementById('inputExprev').value.toUpperCase();
    hda = document.getElementById('inputHda').value.toUpperCase();
    if (box.length < 2) {
      box = '0' + box;
    }
    if (
      box === '' ||
      entrada === '' ||
      nome === '' ||
      dn === '' ||
      hd === '' ||
      ap === '' ||
      alergias === '' ||
      medprev === '' ||
      exprev === '' ||
      hda === '' ||
      status === ''
    ) {
      console.log('ERRO: CAMPO(S) EM BRANCO.');
      swal({
        title: 'ERRO',
        text: 'Por favor, preencha o(s) campo(s) em branco.',
        icon: 'warning',
        dangerMode: true,
      });
    } else {
      sendData();
    }
  };

  const close = () => {
    changeState({
      type: 'PACIENTES_ON',
      payloadUserLogin: loginLogin,
      payloadUserName: loginUsuario,
      payloadCti: loginCti,
    });
  };

  // TOAST.
  var toastColor = '';
  var toastMessage = '';
  const toastSuccess = () => {
    toastColor = '#52BE80';
    toastMessage = 'PACIENTE INSERIDO COM SUCESSO';
    changeState({
      type: 'TOAST_SUCCESS_PACIENTES',
      payloadToastColor: toastColor,
      payloadToastMessage: toastMessage,
      payloadUserLogin: loginLogin,
      payloadUserName: loginUsuario,
      payloadCti: loginCti,
    });
    setTimeout(() => {
      changeState({
        type: 'PACIENTES_OFF',
        payloadUserLogin: loginLogin,
        payloadUserName: loginUsuario,
        payloadCti: loginCti,
      });
      changeState({
        type: 'PACIENTES_ON',
        payloadUserLogin: loginLogin,
        payloadUserName: loginUsuario,
        payloadCti: loginCti,
      });
    }, 3500);
  };

  // varíavel 'status'.
  const clickStatus0 = () => {
    status = 'INSTÁVEL';
  };
  const clickStatus1 = () => {
    status = 'ALERTA';
  };
  const clickStatus2 = () => {
    status = 'ESTÁVEL';
  };

  // impedindo números grandes para o box.
  const fixBox = () => {
    box = document.getElementById('inputBox').value;
    if (box.length > 3) {
      document.getElementById('inputBox').value = '';
    }
  };

  // impedindo o preenchimento incompleto de datas.
  const fixEntrada = (e) => {
    entrada = document.getElementById('inputEntrada').value;
    if (entrada.includes('_')) {
      document.getElementById('inputEntrada').value = '';
      e.target.placeholder = 'ADM';
    }
  };
  const fixDn = (e) => {
    dn = document.getElementById('inputDn').value;
    if (dn.includes('_')) {
      document.getElementById('inputDn').value = '';
      e.target.placeholder = 'DN';
    }
  };

  // eslint-disable-next-line
  if (viewInsertPaciente == 1) {
    return (
      <div
        className="secondary"
        style={{
          backgroundColor: 'rgba(0, 0, 0, 0.6)',
          borderRadius: 0,
          position: 'absolute',
          top: 0,
          bottom: 0,
          left: 0,
          right: 0,
          height: '100%',
          width: '100%',
          marginTop: 0,
          marginBottom: 0,
          marginLeft: 0,
          marginRight: 0,
        }}
      >
        <div>
          <div
            className="secondary"
            style={{
              backgroundColor: '#FFFFFF',
              borderRadius: 5,
              paddingTop: 30,
              paddingBottom: 30,
              paddingLeft: 30,
              paddingRight: 30,
            }}
          >
            <label
              className="title2"
              style={{ marginTop: 0, marginBottom: 15 }}
            >
              INSERIR PACIENTE
            </label>
            <div>
              <div
                style={{
                  display: 'flex',
                  flexDirection: 'row',
                  justifyContent: 'center',
                  marginBottom: 0,
                }}
              >
                <label>
                  <input
                    id="inputBox"
                    className="input"
                    title="BOX."
                    type="number"
                    onChange={() => fixBox()}
                    maxLength="3"
                    placeholder="BOX"
                    autoComplete="off"
                    onFocus={(e) => (e.target.placeholder = '')}
                    onBlur={(e) => (e.target.placeholder = 'BOX')}
                    style={{
                      marginBottom: 0,
                      marginRight: 0,
                      width: 50,
                      height: 50,
                    }}
                  ></input>
                </label>
                <label>
                  <MaskedInput
                    className="input"
                    title="DATA DE ADMISSÃO."
                    placeholder="ADM"
                    autoComplete="off"
                    onFocus={(e) => (e.target.placeholder = '')}
                    onBlur={(e) => fixEntrada(e)}
                    style={{
                      marginBottom: 0,
                      marginRight: 0,
                      width: 100,
                      height: 50,
                    }}
                    mask="11/11/1111"
                    id="inputEntrada"
                  />
                </label>
                <input
                  className="input"
                  title="NOME."
                  placeholder="NOME"
                  autoComplete="off"
                  maxLength="50"
                  onFocus={(e) => (e.target.placeholder = '')}
                  onBlur={(e) => (e.target.placeholder = 'NOME')}
                  style={{
                    marginBottom: 0,
                    width: 440,
                    height: 50,
                  }}
                  type="text"
                  id="inputNome"
                ></input>
                <label>
                  <MaskedInput
                    title="DATA DE NASCIMENTO."
                    autoComplete="off"
                    placeholder="DN"
                    onFocus={(e) => (e.target.placeholder = '')}
                    onBlur={(e) => fixDn(e)}
                    className="input"
                    style={{
                      marginBottom: 0,
                      marginLeft: 0,
                      width: 100,
                      height: 50,
                    }}
                    mask="11/11/1111"
                    id="inputDn"
                  />
                </label>
              </div>
              <div
                style={{
                  display: 'flex',
                  flexDirection: 'row',
                  justifyContent: 'center',
                  marginTop: 5,
                  marginBottom: 0,
                }}
              >
                <div
                  class="radio-toolbar"
                  style={{
                    display: 'flex',
                    flexDirection: 'row',
                    marginTop: 0,
                    marginBottom: 0,
                  }}
                >
                  <input
                    type="radio"
                    id="radio1"
                    name="status"
                    value="ESTÁVEL"
                    onClick={() => clickStatus2()}
                  ></input>
                  <label for="radio1">ESTÁVEL</label>
                  <input
                    type="radio"
                    id="radio2"
                    name="status"
                    value="ALERTA"
                    onClick={() => clickStatus1()}
                  ></input>
                  <label for="radio2">ALERTA</label>
                  <input
                    type="radio"
                    id="radio3"
                    name="status"
                    value="INSTÁVEL"
                    onClick={() => clickStatus0()}
                  ></input>
                  <label for="radio3">INSTÁVEL</label>
                </div>
              </div>
              <div
                style={{
                  display: 'flex',
                  flexDirection: 'row',
                  justifyContent: 'center',
                  marginTop: 0,
                }}
              >
                <textarea
                  className="textarea"
                  title="ANTECEDENTES PESSOAIS."
                  id="inputAp"
                  type="text"
                  maxLength="200"
                  autoComplete="off"
                  placeholder="ANTECEDENTES PESSOAIS"
                  onFocus={(e) => (e.target.placeholder = '')}
                  onBlur={(e) =>
                    (e.target.placeholder = 'ANTECEDENTES PESSOAIS')
                  }
                  style={{
                    resize: 'none',
                    marginBottom: 0,
                    marginRight: 0,
                    width: 350,
                    height: 90,
                  }}
                ></textarea>
                <textarea
                  className="textarea"
                  title="ALERGIAS."
                  id="inputAlergias"
                  type="text"
                  maxLength="200"
                  autoComplete="off"
                  placeholder="ALERGIAS"
                  onFocus={(e) => (e.target.placeholder = '')}
                  onBlur={(e) => (e.target.placeholder = 'ALERGIAS')}
                  style={{
                    resize: 'none',
                    marginBottom: 0,
                    width: 350,
                    height: 90,
                  }}
                ></textarea>
              </div>
              <div
                style={{
                  display: 'flex',
                  flexDirection: 'row',
                  justifyContent: 'center',
                }}
              >
                <textarea
                  className="textarea"
                  title="MEDICAÇÕES PRÉVIAS."
                  id="inputMedprev"
                  type="text"
                  maxLength="200"
                  autoComplete="off"
                  placeholder="MEDICAÇÕES PRÉVIAS"
                  onFocus={(e) => (e.target.placeholder = '')}
                  onBlur={(e) => (e.target.placeholder = 'MEDICAÇÕES PRÉVIAS')}
                  style={{
                    resize: 'none',
                    marginBottom: 0,
                    marginRight: 0,
                    width: 350,
                    height: 90,
                  }}
                ></textarea>
                <textarea
                  className="textarea"
                  title="EXAMES PRÉVIOS."
                  id="inputExprev"
                  type="text"
                  maxLength="200"
                  autoComplete="off"
                  placeholder="EXAMES PRÉVIOS"
                  onFocus={(e) => (e.target.placeholder = '')}
                  onBlur={(e) => (e.target.placeholder = 'EXAMES PRÉVIOS')}
                  style={{
                    resize: 'none',
                    marginBottom: 0,
                    width: 350,
                    height: 90,
                  }}
                ></textarea>
              </div>
              <div
                style={{
                  display: 'flex',
                  flexDirection: 'row',
                  justifyContent: 'center',
                }}
              >
                <textarea
                  className="textarea"
                  title="HIPÓTESES DIAGNÓSTICAS."
                  id="inputHd"
                  type="text"
                  maxLength="200"
                  autoComplete="off"
                  placeholder="HIPÓTESES DIAGNÓSTICAS"
                  onFocus={(e) => (e.target.placeholder = '')}
                  onBlur={(e) =>
                    (e.target.placeholder = 'HIPÓTESES DIAGNÓSTICAS')
                  }
                  style={{
                    marginBottom: 0,
                    marginRight: 0,
                    width: 350,
                    height: 90,
                  }}
                ></textarea>
                <textarea
                  className="textarea"
                  title="HISTÓRIA DA DOENÇA ATUAL."
                  id="inputHda"
                  type="text"
                  maxLength="200"
                  autoComplete="off"
                  placeholder="HISTÓRIA DA DOENÇA ATUAL"
                  onFocus={(e) => (e.target.placeholder = '')}
                  onBlur={(e) =>
                    (e.target.placeholder = 'HISTÓRIA DA DOENÇA ATUAL')
                  }
                  style={{
                    resize: 'none',
                    marginBottom: 0,
                    width: 350,
                    height: 90,
                  }}
                ></textarea>
              </div>
            </div>
            <div
              style={{
                display: 'flex',
                flexDirection: 'row',
                justifyContent: 'center',
              }}
            >
              <button
                className="red-button"
                onClick={() => close()}
                style={{
                  marginTop: 35,
                }}
              >
                CANCELAR
              </button>
              <button
                className="green-button"
                onClick={() => checkBox()}
                style={{
                  marginTop: 35,
                  marginRight: 0,
                }}
              >
                SALVAR
              </button>
            </div>
          </div>
        </div>
      </div>
    );
  } else {
    return null;
  }
}
export default InsertPaciente;
