import axios from 'axios';
import React from 'react';
import swal from 'sweetalert';
import { useSelector, useDispatch } from 'react-redux';
import logo from '../images/logo.png';
import '../design.css';

function Login() {
  var html = 'https://passomatrix-server.herokuapp.com';
  // var html = '//localhost:3000';
  const changeState = useDispatch();

  const viewLogin = useSelector((state) => state.login);

  var login = null;
  var senha = null;

  // função que busca um registro de login válido no servidor.
  const checkLogin = () => {
    login = document.getElementById('inputLogin').value;
    senha = document.getElementById('inputSenha').value;
    var quotes = "'";
    axios
      .get(
        html +
          '/login/' +
          quotes +
          login +
          quotes +
          '/' +
          quotes +
          senha +
          quotes
      )
      .then((res) => {
        var x = []; // se o tipo da variável x não for definido como array [], a função .map abaixo não funciona.
        x = res.data;
        const loginLogin = x.map((login) => login.user);
        const loginUsuario = x.map((login) => login.nome);
        const loginCti = x.map((login) => login.cti);
        setTimeout(function () {
          // eslint-disable-next-line
          if (loginLogin != '' && login != '' && senha != '') {
            console.log(
              'LOGIN EFETUADO COM SUCESSO: USUÁRIO ' + loginUsuario + '.'
            );
            changeState({
              type: 'PACIENTES_ON',
              payloadUserLogin: loginLogin,
              payloadUserName: loginUsuario,
              payloadCti: loginCti,
            });
          } else {
            console.log('FALHA DE LOGIN');
            swal({
              title: 'ERRO',
              text: 'Login ou senha incorretos. Repita a operação.',
              icon: 'warning',
              dangerMode: true,
            });
            changeState({ type: 'LOGIN_OFF' });
            changeState({ type: 'LOGIN_ON' });
          }
        }, 2000);
      });
  };

  if (viewLogin === 1) {
    return (
      <div className="main fade-in">
        <img alt="" src={logo} style={{ height: 85 }}></img>
        <p className="title1" style={{ marginBottom: 0 }}>
          PASSOMATRIX
        </p>
        <p className="title2" style={{ fontSize: 20 }}>
          SEU PASSÔMETRO WEB!
        </p>
        <div>
          <div>
            <input
              autoComplete="off"
              placeholder="LOGIN"
              className="input"
              type="text"
              id="inputLogin"
              onFocus={(e) => (e.target.placeholder = '')}
              onBlur={(e) => (e.target.placeholder = 'LOGIN')}
              style={{
                marginTop: 20,
                marginBottom: 20,
                width: 200,
                height: 50,
              }}
            ></input>
          </div>
          <div>
            <input
              autoComplete="off"
              placeholder="SENHA"
              className="input"
              type="password"
              id="inputSenha"
              onFocus={(e) => (e.target.placeholder = '')}
              onBlur={(e) => (e.target.placeholder = 'SENHA')}
              style={{
                marginTop: 20,
                marginBottom: 20,
                width: 200,
                height: 50,
              }}
            ></input>
          </div>
        </div>
        <div>
          <button
            onClick={() => checkLogin()}
            className="blue-button"
            style={{ width: 100 }}
          >
            OK
          </button>
        </div>
      </div>
    );
  } else {
    return null;
  }
}
export default Login;
